@import url(https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@100;300&display=swap);
body{--yam-color: 247, 145, 116;--yam-color-primary: rgb(var(--yam-color));--yam-color-secondary: rgba(var(--yam-color), 0.1);--correct-color: 107, 217, 98;--correct-color-primary: rgb(var(--correct-color));--correct-color-secondary: rgba(var(--correct-color), 0.1);--incorrect-color: 230, 93, 81;--incorrect-color-primary: rgb(var(--incorrect-color));--incorrect-color-secondary: rgba(var(--incorrect-color), 0.1)}@media(prefers-color-scheme: light){html{--background-color: white;--foreground-color: 0, 0, 0;--foreground-color-primary: rgb(var(--foreground-color));--foreground-color-secondary: rgba(var(--foreground-color), 0.3);--foreground-color-tertiary: rgba(var(--foreground-color), 0.05)}html.dark{--background-color: rgb(13, 17, 23);--foreground-color: 255, 255, 255;--foreground-color-primary: rgba(var(--foreground-color), 0.7);--foreground-color-secondary: rgba(var(--foreground-color), 0.3);--foreground-color-tertiary: rgba(var(--foreground-color), 0.05)}}@media(prefers-color-scheme: dark){html{--background-color: rgb(13, 17, 23);--foreground-color: 255, 255, 255;--foreground-color-primary: rgba(var(--foreground-color), 0.7);--foreground-color-secondary: rgba(var(--foreground-color), 0.3);--foreground-color-tertiary: rgba(var(--foreground-color), 0.05)}html.light{--background-color: white;--foreground-color: 0, 0, 0;--foreground-color-primary: rgb(var(--foreground-color));--foreground-color-secondary: rgba(var(--foreground-color), 0.3);--foreground-color-tertiary: rgba(var(--foreground-color), 0.05)}}body{background-color:var(--background-color);color:var(--foreground-color-primary);margin:72px auto;width:80vw;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
h1{font-size:72px}h3{font-weight:500}button{flex-grow:1;font-size:24px;padding:12px 24px;margin:0 8px;background:none;border:2px solid var(--foreground-color-secondary);color:var(--foreground-color-primary);border-radius:12px}button:hover{background:var(--foreground-color-tertiary);cursor:pointer}button.selected{border-color:var(--yam-color-primary);background:var(--yam-color-secondary)}button.correct{border-color:var(--correct-color-primary);background:var(--correct-color-secondary)}button.incorrect{border-color:var(--incorrect-color-primary);background:var(--incorrect-color-secondary)}.header{text-align:center;font-family:"Londrina Solid",sans-serif;font-size:24px;color:var(--yam-color-primary);margin-bottom:64px}.header *{margin:0}.yam-image{display:block;margin:auto;width:50%;margin-bottom:64px;cursor:pointer}.Question{margin-bottom:64px}.row{display:flex}.results{text-align:center}
